<template>
  <div id="app">
    <el-container>
      <el-aside width="">
        <el-scrollbar style="height:100%">
          <el-menu class="el-menu-vertical-demo" :collapse="isCollapse" background-color="#2f3e4e" text-color="#fff"
            :router="true" active-text-color="#409eff">
            <div class="logo">
              <div class="logobox hidden1" :title="hospitalData.Name" v-if="!isCollapse" style="color: #fff;text-align:center;line-height: 60px;font-size: 22px;">
                {{hospitalData.Name}}
              </div>
            </div>
            <el-menu-item v-for="v in table" v-if="!v.children && v.meta.isshow " :index="v.name" :route="{name:v.name}">
              <i :class="[v.meta.icon]"></i>
              <span slot="title">{{v.meta.title}}</span>
            </el-menu-item>
            <el-submenu v-else-if="v.children && v.meta.isshow " :index="v.name">
              <template slot="title">
                <i :class="[v.meta.icon]"></i>
                <span slot="title">{{v.meta.title}}</span>
              </template>
              <el-menu-item v-for="val in v.children" :index="val.name" :route="{name:val.name}"><i :class="[val.meta.icon]"></i><span>{{val.meta.title}}</span></el-menu-item>
            </el-submenu>
          </el-menu>
        </el-scrollbar>
      </el-aside>
      <el-container style="width: 100%">
        <el-header style="text-align: right; font-size: 12px;background: #fff;border-bottom: 1px solid #e6e6e6;">
          <div id="fll" @click="dsplins">
            <i class="el-icon-sanhengxian1" style="color:#333;  font-size: 25px; line-height: 50px;" ref="fll"></i>
          </div>
          <div class="header-right">
                  <el-dropdown-item icon="el-icon-tuichudenglu" @click.native="tuichu">退出登录</el-dropdown-item>
          </div>
        </el-header>
        <el-main>
          <Crumbs></Crumbs>
          <router-view />
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
  import Crumbs from '@/components/Crumbs.vue'
  export default {
    data() {
      return {
        isCollapse: false,
        table: [],
        hospitalData: ''
      };
    },
    components: {
      Crumbs
    },
    methods: {
      dsplins() {
        if (!this.isCollapse) {
          this.$refs.fll.className = "el-icon-sanhengxian";
          this.isCollapse = true;
        } else {
          this.$refs.fll.className = "el-icon-sanhengxian1";
          this.isCollapse = false;
        }
      },
      open() {
        this.$router.push({
          name: 'password'
        })
      },
      tuichu() {
        this.$router.push({
          name: 'login'
        })
        sessionStorage.removeItem('hospitalCookie')
      }
    },
    mounted() {
       let hospitalDatas = sessionStorage.getItem("hospitalData")
       this.hospitalData =JSON.parse(hospitalDatas)
      this.table = this.$router.options.routes[1].children
    }
  }
</script>
<style scoped>
  #app {
    width: 100vw;
    height: 100vh;
    overflow: auto;
    position: relative;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
    height: 100vh;
  }

  .el-menu--collapse {
    height: 100vh;
  }

  .logo {
    height: 60px;
    position: relative;
  }

  .logobox {
    width: 200px;
    position: absolute;
    bottom: 0;
  }

  .logo img {
    width: 64px;
  }

  body,
  html {
    width: 100%;
    height: 100%;
  }

  .el-menu-item,
  .el-submenu,
  .el-submenu__title {
    color: #fff;
  }

  .el-submenu__title span {
    color: #fff;
  }

  .el-aside .el-menu a {
    display: block;
  }

  #app {
    height: 100%;
  }

  .el-header,
  .el-footer {
    background-color: #f3f3f3;
    color: #333;
    text-align: center;
    line-height: 60px;
    width: 100%;
    height: 10%;
  }

  .el-container {
    height: 100vh;
  }

  .el-main {
    color: #333;
    height: 100%;
    overflow: auto;
  }

  .el-menu-item {
    background-color: #000;
  }

  body>.el-container {
    margin-bottom: 40px;
  }

  .el-header {
    position: relative;
  }

  #fll {
    position: absolute;
    top: 5px;
    left: 0px;
    width: 50px;
    height: 50px;
    cursor: pointer;

  }

  /* .el-dropdown{
        margin-right: 20px;
    }
    .el-dropdown-link {
        cursor: pointer;
        color: #909399;
    }
    .el-icon-arrow-down {
        font-size: 12px;
    } */
  .header-right {
    float: right;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .header-right>div {
    float: left;
    color: #909399;
    height: 60px;
    line-height: 60px;
    padding: 0 20px;
    cursor: pointer;
  }

  .el-dropdown-link:hover {
    color: #303133;
  }

  .header-right i {
    font-size: 15px;
    line-height: 60px;
    margin-right: 5px;
  }
</style>
